import Vue from 'vue'
import Router from 'vue-router'
import home from '@/components/pages/home'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'home',
      component: home,
      meta: { title: 'HomePage' }
    }
  ]
})



